import React, { useEffect } from 'react';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { useLocation, useHistory } from 'react-router-dom';
import { Typography, Grid, Button } from '@material-ui/core';
import { toast } from 'react-toastify';

import { Field, Header } from '../../components';
import { signIn, signUp } from '../../services/api';
import { PAGES } from '../../constants';

import { schema } from './schema';
import { useStyles } from './styles';

const SIGN_IN_FIELDS = [
    { name: 'email', placeholder: 'E-mail', type: 'text' },
    { name: 'password', placeholder: 'Password', type: 'password' },
];

const SIGN_UP_FIELDS = [
    { name: 'email', placeholder: 'E-mail', type: 'text '},
    { name: 'password', placeholder: 'Password', type: 'password' },
    {
        name: 'passwordRepeat',
        placeholder: 'Password Repeat',
        type: 'password',
    },
];

export function AuthPage({
    type,
    title,
    annotationText,
    submitButtonTitle,
}) {
    const classes = useStyles();
    const location = useLocation();
    const history = useHistory();

    const { handleSubmit, register, errors, reset } = useForm({
        defaultValues: {
            email: '',
            password: '',
            passwordRepeat: '',
        },
        resolver: yupResolver(schema),
        context: { isSignUp: type === 'signUp' },
    });

    useEffect(() => {
        const token = localStorage.getItem('token');

        if (token) {
            history.push(PAGES.CHART);
        }
    }, [history]);

    useEffect(() => {
        reset();
    }, [location, reset]);

    const handleSignIn = async (credentials) => {
        try {
            const { token } = await signIn(credentials);
            localStorage.setItem('token', token);

            history.push(PAGES.CHART);
        } catch {
            toast.error('Incorrect login or password', {
                position: 'top-right',
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: false,
                progress: undefined,
            });
        }
    };

    const handleSignUp = async ({ email, password }) => {
        try {
            await signUp({ email, password });

            const { token } = await signIn({ email, password });

            localStorage.setItem('token', token);

            history.push(PAGES.CHART);
        } catch {
            toast.error('Something went wrong during registration', {
                position: 'top-right',
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: false,
                progress: undefined,
            });
        }
    };

    const handleFormSubmit = async (data) =>
        type === 'signIn' ? handleSignIn(data) : handleSignUp(data);

    const fields = type === 'signUp' ? SIGN_UP_FIELDS : SIGN_IN_FIELDS;

    return (
        <Grid
            className={classes.container}
            container
            spacing={2}
            justify={'center'}
            alignContent={'center'}
        >
            <Header title={title} />
            <Grid item xs={3}>
                <Grid container justify={'center'} direction={'column'}>
                    {fields.map((field) => (
                        <Field
                            key={field.name}
                            {...field}
                            error={!!errors[field.name]}
                            helperText={
                                !!errors[field.name] &&
                                errors[field.name].message
                            }
                            inputRef={register}
                        />
                    ))}
                    <Button
                        className={classes.signInButton}
                        color={'primary'}
                        variant={'outlined'}
                        onClick={handleSubmit(handleFormSubmit)}
                    >
                        {submitButtonTitle}
                    </Button>
                    <Typography align={'center'} variant={'overline'}>
                        {annotationText}
                    </Typography>
                </Grid>
            </Grid>
        </Grid>
    );
}
