import React from 'react';
import { TextField } from '@material-ui/core';
import { useStyles } from './style';

export function Field({
    type,
    name,
    inputRef,
    error,
    helperText,
    placeholder,
}) {
    const classes = useStyles();

    switch (type) {
        default:
            return (
                <TextField
                    type={type}
                    key={name}
                    inputRef={inputRef}
                    name={name}
                    className={classes.input}
                    variant={'outlined'}
                    error={error}
                    helperText={helperText}
                    placeholder={placeholder}
                />
            );
    }
}
