import { webSocket } from 'rxjs/webSocket';
import { catchError } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { toast } from 'react-toastify';

export const subscribeToStockPrice = (symbol) => {
    const token = localStorage.getItem('token');

    return webSocket(
        `ws://localhost:8000/stockPrice?token=${token}&stockSymbol=${symbol}`
    ).pipe(
        catchError((_) => {
            toast.error(`Can't connect to ${symbol} price`, {
                position: 'top-right',
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: false,
                progress: undefined,
            });
            return EMPTY;
        })
    );
};

