import React from 'react';
import { format } from 'date-fns';
import { Paper, Typography, makeStyles } from '@material-ui/core';

const useStyles = makeStyles(() => ({
    newsContainer: {},
    newsItemContainer: {
        padding: '10px',
        marginBottom: '10px'
    },
    image: {
        width: '100%',
        height: 'auto'
    }
}));

export function NewsItem({ title, description, date, url, imageUrl }) {
    const classes = useStyles();

    return (
        <Paper className={classes.newsItemContainer} elevation={3}>
            <Typography variant={'h5'}>
                <a href={url}>
                    {title}
                </a>
            </Typography>
            <Typography variant={'body1'}>{description}</Typography>
            <Typography variant={'body1'}>
                {format(date, 'dd MMM yyyy HH:mm:ss')}
            </Typography>
            <img className={classes.image} src={imageUrl} alt='image url' />
        </Paper>
    );
}
