import React from 'react';
import {
    CartesianGrid,
    Line,
    LineChart,
    ResponsiveContainer,
    Tooltip,
    XAxis,
    YAxis,
} from 'recharts';

export function Chart({ data }) {
    return (
        <ResponsiveContainer width={'95%'}>
            <LineChart data={data}>
                <Line
                    type='monotone'
                    dataKey='value'
                    stroke='#8884d8'
                    isAnimationActive={false}
                />
                <Tooltip />
                <CartesianGrid stroke='#ccc' />
                <XAxis dataKey='label' />
                <YAxis dataKey='value' />
            </LineChart>
        </ResponsiveContainer>
    );
}
