import React, { useState, useEffect } from 'react';
import { Grid } from '@material-ui/core';
import { format } from 'date-fns';

import {
    debounceTime,
    switchMap,
    tap,
    map,
    withLatestFrom,
} from 'rxjs/operators';

import {
    Header,
    Chart,
    CompanyInput,
    LogOutButton,
    News,
} from '../../components';

import { useStyles } from './styles';
import { EMPTY, Subject, combineLatest } from 'rxjs';

import { subscribeToStockPrice, subscribeToNews } from '../../services/api';

// STREAMS
const companySymbol$ = new Subject();

const keywords$ = new Subject();

const companySymbolSearchInput$ = companySymbol$.pipe(debounceTime(500));

const keywordsSearchInput$ = keywords$.pipe(
    debounceTime(500),
    map((keywords) =>
        keywords
            .split(',')
            .map((key) => key.trim())
            .join(',')
    )
);

export function ChartPage() {
    const classes = useStyles();

    const [companySymbol, setCompanySymbol] = useState('');
    const [keywords, setKeywords] = useState('');
    const [stockPrices, setStockPrices] = useState([]);
    const [news, setNews] = useState([]);

    useEffect(() => {
        const companySymbolSub = companySymbol$.subscribe(setCompanySymbol);

        const keywordsSub = keywords$.subscribe(setKeywords);

        const stockPricesSub = companySymbolSearchInput$
            .pipe(
                tap(() => setStockPrices([])),
                switchMap((symbol) =>
                    symbol.length ? subscribeToStockPrice(symbol) : EMPTY
                )
            )
            .subscribe((price) => {
                setStockPrices((prev) => [
                    ...prev,
                    { label: format(new Date(), 'HH:mm:ss'), value: price },
                ]);
            });

        const newsSub = combineLatest(
            companySymbolSearchInput$,
            keywordsSearchInput$
        )
            .pipe(
                tap(() => setNews([])),
                switchMap(([symbol, keywords]) =>
                    symbol.length && keywords.length
                        ? subscribeToNews(symbol, keywords)
                        : EMPTY
                )
            )
            .subscribe((news) => {
                setNews((prev) => [
                    ...prev,
                    {
                        title: news.title,
                        description: news.summary,
                        date: news.datetime,
                        url: news.url,
                        imageUrl: news.imageUrl
                    },
                ]);
            });

        return () => {
            stockPricesSub.unsubscribe();
            companySymbolSub.unsubscribe();
            newsSub.unsubscribe();
            keywordsSub.unsubscribe();
        };
    }, []);

    const handleCompanySymbolChange = (event) => {
        companySymbol$.next(event.target.value);
    };

    const handleKeywordsChange = (event) => {
        keywords$.next(event.target.value);
    };

    return (
        <Grid
            className={classes.container}
            container
            spacing={2}
            justify={'center'}
            alignItems={'center'}
        >
            <Header title={'Chart Page'} />
            <Grid container>
                <Grid item xs={11}>
                    <Grid
                        container
                        justify={'flex-end'}
                        className={classes.controlsContainer}
                    >
                        <CompanyInput
                            label={'Enter Symbol'}
                            value={companySymbol}
                            onChange={handleCompanySymbolChange}
                        />
                        <CompanyInput
                            label={'Enter Keywords'}
                            value={keywords}
                            onChange={handleKeywordsChange}
                        />
                        <LogOutButton />
                    </Grid>
                </Grid>
                <Grid item xs={1} />
            </Grid>

            <Grid container className={classes.contentContainer}>
                <Grid item xs={1} />
                <Grid className={classes.chartContainer} item xs={7}>
                    <Chart data={stockPrices} />
                </Grid>
                <Grid className={classes.newsContainer} item xs={3}>
                    <News news={news} />
                </Grid>
                <Grid item xs={1} />
            </Grid>
        </Grid>
    );
}
