import React from 'react';
import { makeStyles, TextField } from '@material-ui/core';

const useStyles = makeStyles(() => ({
    companySelector: {
        width: '200px',
        marginRight: '20px',
        marginLeft: '20px',
    },
}));

export function CompanyInput({ onChange, value, label }) {
    const classes = useStyles();

    return (
        <TextField
            variant={'outlined'}
            className={classes.companySelector}
            label={label}
            value={value}
            onChange={onChange}
        />
    );
}
